import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

import { MessageService } from 'primeng/api';

import { StorageService } from 'src/app/services/storage-service.service';
import { AlertController, ModalController } from '@ionic/angular';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { RpgService } from 'src/app/services/rpg/rpg.service';
import { RPGBasicUpdateDTO } from 'src/app/models/rpg/rpg-basic-update.dto';

@Component({
  selector: 'app-rpg-update',
  templateUrl: './rpg-update.component.html',
  styleUrls: ['./rpg-update.component.scss'],
})
export class RpgUpdateComponent implements OnInit {

  @Input('menu')
  menu: boolean = true;
  @Input('cancelarBTN')
  cancelarBTN: boolean = false;

  @Input('add')
  add: boolean = false;

  formRPG: FormGroup;
  newRPG: RPGBasicUpdateDTO;

  loading: boolean = false;
  display: boolean = false;

  idCriado: string = '';
  novo: boolean = false;

  paginaAtual = 0;
  paginaAtualMissao = 0;

  requestUpdate: boolean = false;

  @Output('obj')
  obj: EventEmitter<any> = new EventEmitter();

  constructor(private formBuilder: FormBuilder,
    private storage: StorageService,
    private alertController: AlertController,
    private rpgService: RpgService,
    private paginaService: PaginacaoService,
    private toast: MessageService,
    public modalCtrl: ModalController) {
    this.formRPG = this.formBuilder.group({
      nome: ['', [Validators.required]],
      descricao: ['', [Validators.required]],
      historia: ['', [Validators.required]],
      imgURL: ['', [Validators.required]],
      isPrivate: ['', []],
    });
  }

  ngOnInit() {
    this.requestUpdate = false;
    this.loading = true;


    const id = this.rpgService.getStaticID();
    this.rpgService.findById(id)
      .subscribe(res => {

        const { idMapa, descricao, nome, imgURL, isPrivate, historia } = res;

        this.formRPG.get('nome').setValue(nome);
        this.formRPG.get('descricao').setValue(descricao);
        this.formRPG.get('historia').setValue(historia);
        this.formRPG.get('imgURL').setValue(imgURL);
        if (isPrivate == false) {
          this.formRPG.get('isPrivate').setValue(false);
        } else {
          this.formRPG.get('isPrivate').setValue(true);
        }

        this.loading = false;
      },
        error => {
          console.log(error)
          this.loading = false;
        });
  }


  salvar() { //Implementar a atualização e deixar bonito
    const idRpg = this.rpgService.getStaticID();
    this.requestUpdate = true;
    this.loading = true;
    this.newRPG = {
      idRpg,
      nome: this.formRPG.get('nome').value,
      descricao: this.formRPG.get('descricao').value,
      historia: this.formRPG.get('historia').value,
      imgURL: this.formRPG.get('imgURL').value,
      isPrivate: this.formRPG.get('isPrivate').value
    };

    

     this.rpgService.updateBasic(this.newRPG)
       .subscribe(res => {
         this.loading = false;
         this.callToast('success', 'Atualizado com sucesso!', 'RPG atualizado');
       },
         error => {
           this.loading = false;
         });

  }

  novoMapa() {
    this.display = false;
    this.novo = true;
  }

  listar() {
    this.display = true;
    this.novo = false;
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  consultar() {
    const p = this.rpgService.findPageByCriador(this.paginaAtual.toString());
    this.paginaService.setPromisse(p);
    this.listar();
  }

  async presentAdicionar(id: string) {
    const alert = await this.alertController.create({
      header: 'Atualizado com sucesso!',
      message: 'Código: ' + id,
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            this.copyToClipboard(id);
          }
        }
      ]
    });

    await alert.present();
  }

  async copyToClipboard(value: string) {
    let id = (e: ClipboardEvent) => {
      e.preventDefault();
      e.clipboardData.setData('text', (value));

    };
    document.addEventListener('copy', id);
    document.execCommand('copy');
    document.removeEventListener('copy', null);
  }

  close() {
    this.modalCtrl.dismiss(this.requestUpdate, null, 'princi');
  }

  callToast(severity, summary, detail) {
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

}
