import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { AlertController, ModalController } from '@ionic/angular';
import { RpgUpdateComponent } from '../components/criacoes/rpg-update/rpg-update.component';
import { RpgService } from '../services/rpg/rpg.service';
import { StorageService } from '../services/storage-service.service';
import { PaginacaoService } from '../services/utils/paginacao.service';

@Component({
  selector: 'app-gerrpg',
  templateUrl: './gerrpg.page.html',
  styleUrls: ['./gerrpg.page.scss'],
})
export class GerrpgPage implements OnInit {

  novo: boolean = false;
  paginaAtual = 0;
  paginaAtualMissao = 0;
  loading: boolean = false;
  display: boolean = false;

  @Output('obj')
  obj: EventEmitter<any> = new EventEmitter();

  constructor(
    private storage: StorageService,
    private paginaService: PaginacaoService,
    private modalCtrl: ModalController,
    private rpgService: RpgService,
  ) { }

  ngOnInit() {
  }

  novoRPG() {
    this.display = false;
    this.novo = true;
  }

  listar() {
    this.display = true;
    this.novo = false;
  }

  consultar() {
    const p = this.rpgService.findPageByCriador(this.paginaAtual.toString());
    this.paginaService.setPromisse(p);
    this.listar();
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  async selectedObj(event) {
    this.rpgService.setStaticID(event.id)
    const modal = await this.modalCtrl.create({
      component: RpgUpdateComponent,
      backdropDismiss: false,
      cssClass: 'my-custom-modal-css',
      id: 'princi'
    });

    modal.onDidDismiss()
      .then(res => {
        const { data } = res;
        //console.log(res);
        if (data == true) {
          const p = this.rpgService.findPageByCriador(this.paginaAtual.toString());
          this.paginaService.setPromisse(p);
          this.paginaService.requestRefresh(true);
        }
      });

    return await modal.present();
  }

  getSelectedObj(evt) {
    this.obj.emit(evt);
  }

  actionObj(event) {
    if (this.rpgService.getDoUpdate()) {
      this.selectedObj(event);
    } else {
      this.getSelectedObj(event);
    }
  }

}
